<template>
  <div>
    <div class="title">
      <h3>教师资料管理</h3>
    </div>

    <div class="title">
      <div style="text-align: left; margin: 20px">
        <el-input
          placeholder="请输入教师名"
          v-model="inputteacher"
          clearable
          style="width: 200px; margin-right: 10px"
        ></el-input>
        <el-input
          placeholder="请输入职称"
          v-model="inputpro"
          clearable
          style="width: 200px; margin-right: 10px"
        ></el-input>
        <el-button icon="el-icon-search" round @click="load(1)">查询</el-button>
        <el-button icon="el-icon-search" round @click="reset">重置</el-button>
      </div>
      <div style="margin: 0px 20px">
        <el-button
          icon="el-icon-circle-plus-outline"
          round
          size="small"
          type="primary"
          plain
          @click="handleadd"
          >新增</el-button
        >
        <el-button
          icon="el-icon-delete"
          round
          size="small"
          type="danger"
          plain
          @click="delbatch"
          >批量删除</el-button
        >
      </div>
      <div>
        <el-table
          :data="tableData"
          style="width: 95%; margin-left: 20px; margin-top: 10px"
          height="350"
          :header-cell-style="{
            backgroundColor: 'aliceblue',
            color: '#666',
          }"
          @selection-change="handleSelectionChange"
        >
          <el-table-column
            type="selection"
            width="55"
            align="center"
          ></el-table-column>
          <el-table-column prop="id" label="教师号"></el-table-column>
          <el-table-column prop="tname" label="姓名"></el-table-column>
          <el-table-column prop="sex" label="性别"></el-table-column>
          <el-table-column prop="endata" label="入职日期"></el-table-column>
          <el-table-column prop="pro" label="职称"></el-table-column>
          <el-table-column prop="salary" label="工资"></el-table-column>
          <el-table-column prop="phone" label="手机号"></el-table-column>
          <el-table-column prop="name" label="所属学院"></el-table-column>
          <el-table-column
            label="操作"
            width="200"
            align="center"
            v-slot="scope"
          >
            <el-button
              type="primary"
              size="small"
              @click="handleEdit(scope.row)"
              >编辑</el-button
            >
            <el-button type="danger" size="small" @click="del(scope.row.id)"
              >删除</el-button
            >
          </el-table-column>
        </el-table>
      </div>
      <div style="margin: 10px 100px">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pageSize"
          layout="total,prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="教师信息"
      :visible.sync="dialogFormVisible"
      style="margin-left: 400px; width: 800px; overflow: hidden"
    >
      <el-form :model="addteacher" class="Dialog">
        <el-form-item
          label="教师姓名"
          :label-width="formLabelWidth"
          prop="tname"
        >
          <el-input v-model="addteacher.tname" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="formLabelWidth" prop="sex">
          <!-- <el-input v-model="addteacher.sex" style="width: 200px"></el-input> -->
          <el-radio v-model="addteacher.sex" label="男"></el-radio>
          <el-radio v-model="addteacher.sex" label="女"></el-radio>
        </el-form-item>
        <el-form-item
          label="入职日期"
          :label-width="formLabelWidth"
          prop="endata"
        >
          <div class="block">
            <el-date-picker
              v-model="addteacher.endata"
              type="date"
              placeholder="选择日期"
              :picker-options="time_option"
              style="width: 200px"
            >
            </el-date-picker>
          </div>
        </el-form-item>
        <el-form-item label="职称" :label-width="formLabelWidth" prop="pro">
          <el-input
            v-model="addteacher.pro"
            autocomplete="off"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item label="工资" :label-width="formLabelWidth" prop="salary">
          <el-input
            v-model="addteacher.salary"
            autocomplete="off"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item label="电话" :label-width="formLabelWidth" prop="phone">
          <el-input
            v-model="addteacher.phone"
            autocomplete="off"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="所属学院"
          :label-width="formLabelWidth"
          prop="name"
        >
          <!-- <el-input
            v-model="addteacher.name"
            autocomplete="off"
            style="width: 200px"
          ></el-input> -->
          <el-select v-model="addteacher.collegeId" style="width: 200px">
              <!-- :label-:想要在选项中展示的内容
              :value:这个选项对应的真正的值（因为存进去的其实是部门id，并不是部门名称。比如示例中区域对应的其实是shanghai）
              :key:与value一致 -->
              <el-option
                v-for="item in options"
                :key="item.id"  
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
    
    <script>
export default {
  data() {
    return {
      // 今天之后的日期不能选择
      time_option:{
        disabledDate(time){
          return time.getTime() >Date.now();
        }
      },
      inputteacher: "",
      inputpro: "",
      pageNum: 1, //当前页码
      pageSize: 5, //每页显示的个数
      total: 0,
      dialogFormVisible: false,
      formLabelWidth: "120px",
      ids: [],
      options:[],
      tableData: [
        {
          id: "",
          tname: "",
          phone: "",
          sex: "",
          endata: "",
          salary: "",
          pro: "",
          collegeId: "",
          name: "",
        },
      ],
      addteacher: {
        id: "",
        tname: "",
        phone: "",
        sex: "",
        endata: "",
        salary: "",
        pro: "",
        collegeId: "",
        name: "",
      },
    };
  },
  // 页面触发后完成数据渲染
  // mounted() {
  // axios.get("http://localhost:9091/Student/selectAll").then(res =>{
  //     // 显示数据到后台
  //     console.log(res.data)
  //     this.tableData =res.data.data
  // })
  //   request.get("/teacher").then((res) => {
  //     this.tableData = res.data;
  //   });
  // },
  created() {
    this.load();
    this.getCollege();
  },
  methods: {
    getCollege() {
      //获取所有学院的方法
      this.$request.get("/college/selectALL").then((res)=>{
        if (res.code === "200"){
          this.options=res.data
        }
        else{
          this.$message.error(res.msg)
        }
      })
    },
    del(id) {
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request.delete("/teacher/delete/" + id).then((res) => {
            if (res.code === "200") {
              this.$message.success("删除成功！");
              this.load(1);
            } else {
              this.$message.error(res.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    delbatch() {
      if (!this.ids.length) {
        this.$message.warning("请选择批量删除的数据");
        return;
      }
      this.$confirm("此操作将批量删除这些数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request
            .delete("/teacher/delete/batch", {
              data: this.ids,
            })
            .then((res) => {
              if (res.code === "200") {
                this.$message.success("删除成功！");
                this.load(1);
              } else {
                this.$message.error(res.msg);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 获取表格数据
    handleSelectionChange(rows) {
      // 当前选中的所有的行数据
      this.ids = rows.map((v) => v.id);
    },
    handleEdit(row) {
      //编辑数据
      this.addteacher = JSON.parse(JSON.stringify(row));
      this.dialogFormVisible = true;
    },
    handleadd() {
      //新增数据
      this.addteacher = {};
      this.dialogFormVisible = true;
    },
    save() {
      // this.$refs["reform"].validate((valid) => {
      //   if (valid) {
      this.$request({
        url: this.addteacher.id ? "/teacher/update" : "/teacher/add",
        method: this.addteacher.id ? "PUT" : "POST",
        data: this.addteacher,
      }).then((res) => {
        if (res.code === "200") {
          this.$message.success("保存成功");
          this.load(1);
          this.dialogFormVisible = false;
        } else {
          this.$message.error(res.msg);
        }
        //   });
        // }
      });
    },
    reset() {
      this.inputteacher = "";
      this.inputpro = "";
      this.load();
    },
    load(pageNum) {
      //分页查询
      if (pageNum) {
        this.pageNum = pageNum;
      }
      this.$request
        .get("/teacher/selectByPage", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            tName: this.inputteacher,
            pro: this.inputpro,
          },
        })
        .then((res) => {
          this.tableData = res.data.records;
          this.total = res.data.total;
        });
    },
    handleCurrentChange(pageNum) {
      // this.pageNum = pageNum;
      this.load(pageNum);
    },
  },
};
</script>