<template>
  <div>
    <div class="title">
      <h3>授课信息管理</h3>
    </div>

    <div class="title">
      <div style="height: 480px">
        <el-table
          :data="FingTeachingForm"
          style="width: 95%; height: 250; margin-left: 25px; margin-top: 15px"
          :header-cell-style="{
            backgroundColor: 'aliceblue',
            color: '#666',
          }"
        >
          <el-table-column prop="cname" label="课程名称"></el-table-column>
          <el-table-column prop="type" label="课程类型"></el-table-column>
          <el-table-column prop="grade" label="课程学分"></el-table-column>
          <el-table-column prop="clName" label="授课班级"></el-table-column>
          <el-table-column prop="term" label="授课学期"></el-table-column>
        </el-table>

        <div style="margin: 10px 50px">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            layout="total,prev, pager, next"
            :total="total"
          >
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
    
    <script>
import request from "@/utils/request";
export default {
  data() {
    return {
      pageNum: 1, //当前页码
      pageSize: 10, //每页显示的个数
      total: 0,
      options: [
        // {
        //   value: " ",
        //   label: "必修选修",
        // },
        {
          value: "必修",
          label: "必修",
        },
        {
          value: "选修",
          label: "选修",
        },
      ],
      FingTeachingForm: [
        {
          id: "",
          cname: "",
          type: "",
          grade: "",
          clName: "",
          term: "",
        },
      ],
      user: JSON.parse(localStorage.getItem("honey-user") || "{}"),
    };
  },
  created() {
    this.load();
  },
  methods: {
    load(pageNum) {
      //分页查询
      if (pageNum) {
        this.pageNum = pageNum;
      }
      this.$request
        .get("/lesson/selectByTeacher", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            username: this.user.username
          },
        })
        .then((res) => {
          this.FingTeachingForm = res.data.records;
          this.total = res.data.total;
        });
    },
    handleCurrentChange(pageNum) {
      // this.pageNum = pageNum;
      this.load(pageNum);
    },
  },
};
</script>
    
<style>
.score-Form {
  width: 70%;
  height: 20%;
}
</style>