<template>
    <div>
      <div class="title">
        <h3>学生成绩管理</h3>
      </div>
  
      <div class="title">
            <div style="height: 480px;margin-top: 20px;">
              <el-table
                :data="tableData"
                style="width: 95%; height=: 250; margin-left: 30px"
                :header-cell-style="{
                  backgroundColor: 'aliceblue',
                  color: '#666',
                }"
              >
                <el-table-column
                  prop="sid"
                  label="序号"
                ></el-table-column>
                <el-table-column
                  prop="cname"
                  label="课程名称"
                ></el-table-column>
                <el-table-column
                  prop="tname"
                  label="授课老师"
                ></el-table-column>
                <el-table-column
                  prop="id"
                  label="学生学号"
                ></el-table-column>
                <el-table-column
                  prop="name"
                  label="学生姓名"
                ></el-table-column>
                <el-table-column
                  prop="score"
                  label="考试成绩"
                ></el-table-column>
              </el-table>
  
              <div style="margin: 10px 30px">
                <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="pageNum"
                  :page-sizes="[10, 20, 30, 40]"
                  :page-size="pageSize"
                  layout="total,prev, pager, next"
                  :total="total"
                >
                </el-pagination>
              </div>
            </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        user: JSON.parse(localStorage.getItem("honey-user") || "{}"),
        id: "",
        cName: "",
        name: "",
        score: "",
        teacherId:"",
        pageNum: 1, //当前页码
        pageSize: 10, //每页显示的个数
        total: 0,
        dialogFormVisible: false,
        formLabelWidth: "120px",
        ids: [],
        tableData: [
          {
            sid: "",
            cname: "",
            id: "",
            name: "",
            score: "",
          },
        ],
      };
    },
    created() {
      this.load();
    },
    methods: {
      load(pageNum) {
        //分页查询
        if (pageNum) {
          this.pageNum = pageNum;
        }
        this.$request
          .get("/select/selectByPage", {
            params: {
              pageNum: this.pageNum,
              pageSize: this.pageSize,
              cName: this.cName,
              name: this.name,
              teacherId:this.teacherId,
              id: this.user.username,
              score: this.score,
            },
          })
          .then((res) => {
            this.tableData = res.data.records;
            this.total = res.data.total;
          });
      },
      handleCurrentChange(pageNum) {
        // this.pageNum = pageNum;
        this.load(pageNum);
      },
    },
  };
  </script>
  
  <style>
  .score-Form {
    width: 70%;
    height: 20%;
  }
  </style>