<template>
  <div>
    <div class="title">
      <h3>信息管理</h3>
    </div>

    <div class="title">
      <div class="select">
        <el-form
          :model="FindCource"
          status-icon
          ref="ruleForm"
          label-width="100px"
        >
          <el-row>
            <!-- <el-col :span="4"
              ><div class="grid-content bg-purple">
                <el-form-item label="课程编号：">
                  <el-input type="text" v-model="id"></el-input>
                </el-form-item></div
            ></el-col> -->
            <el-col :span="4"
              ><div class="grid-content bg-purple-light">
                <el-form-item label="课程名称：">
                  <el-input type="text" v-model="cName"></el-input>
                </el-form-item></div
            ></el-col>
            <el-col :span="4"
              ><div class="grid-content bg-purple">
                <el-form-item label="课程学分：">
                  <el-input type="text" v-model="grade"></el-input>
                </el-form-item></div
            ></el-col>

            <el-col :span="4"
              ><div class="grid-content bg-purple-light">
                <el-form-item label="课程类型：">
                  <el-select v-model="type" clearable placeholder="请选择">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    >
                    </el-option>
                  </el-select>
                </el-form-item></div
            ></el-col>
            <el-col :span="5"
              ><div class="grid-content bg-purple">
                <el-form-item>
                  <el-button @click="load(1)">查询</el-button>
                  <el-button @click="reset">重置</el-button>
                </el-form-item>
              </div></el-col
            ></el-row
          >
        </el-form>
      </div>
      <div style="margin: 0px 60px"  v-if="user.auth==='管理员'">
        <el-button
          icon="el-icon-circle-plus-outline"
          round
          size="small"
          type="primary"
          plain
          @click="handleadd"
          >新增</el-button
        >
        <el-button
          icon="el-icon-delete"
          round
          size="small"
          type="danger"
          plain
          @click="delbatch"
          >批量删除</el-button
        >
      </div>
      <div style="height: 300px">
        <el-table
          :data="CourceForm"
          style="width: 95%"
          height="300"
          class="select"
          :header-cell-style="{
            backgroundColor: 'aliceblue',
            color: '#666',
          }"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55" align="center">
          </el-table-column>
          <!-- fixed固定作用 -->
          <el-table-column
            prop="id"
            label="课程编号"
            v-if="user.auth!='学生'"
          ></el-table-column>
          <el-table-column
            prop="cName"
            label="课程名称"
          ></el-table-column>
          <el-table-column
            prop="tname"
            label="授课老师"
            align="center"
            v-if="user.auth==='学生'"
          ></el-table-column>
          <el-table-column
            prop="type"
            label="课程类型"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="grade"
            label="课程学分"
            align="center"
          ></el-table-column>
          <el-table-column
            label="操作"
            align="center"
            v-slot="scope"
            v-if="user.auth!='教师'"
          >
            <el-button
              type="primary"
              size="small"
              @click="handleEdit(scope.row)"
              v-if="user.auth==='管理员'"
              >编辑</el-button
            >
            <el-button type="danger" size="small" @click="del(scope.row.id)"
            v-if="user.auth==='管理员'"
              >删除</el-button
            >
            <el-button type="primary" size="small" @click="handleEdit(scope.row)"
                v-if="user.auth==='学生'"
                  >选课</el-button
                >
          </el-table-column>
        </el-table>
      </div>
      <div style="margin: 10px 50px">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pageSize"
          layout="total,prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="课程信息"
      :visible.sync="dialogFormVisible"
      style="
        margin-top: 20px;
        margin-left: 400px;
        width: 800px;
        overflow: hidden;
      "
      class="DialogFrom"
    >
      <el-form :model="FindCource" class="Dialog" style="width: 200px">
        <!-- <el-form-item label="课程编号" :label-width="formLabelWidth" prop="id"> -->
        <!-- <el-input v-model="FindCource.id" style="width: 200px"></el-input> -->
        <!-- </el-form-item> -->
        <el-form-item
          label="课程名称"
          :label-width="formLabelWidth"
          prop="cName"
          v-if="user.auth==='管理员'"
        >
          <el-input
            v-model="FindCource.cName"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="课程名称"
          :label-width="formLabelWidth"
          prop="cName"
          v-if="user.auth==='学生'"
        >
        <el-select v-model="FindCource.cName" style="width: 200px">
              <el-option
                v-for="item in cource"
                :key="item.id"  
                :label="item.cName"
                :value="item.id"
              >
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item
          label="授课老师"
          :label-width="formLabelWidth"
          prop="tname"
          v-if="user.auth==='学生'"
        >
          <el-select v-model="FindCource.tname" style="width: 200px">
              <el-option
                v-for="item in teacher"
                :key="item.id"  
                :label="item.tname"
                :value="item.id"
              >
              </el-option>
            </el-select>
        </el-form-item>
        <!-- <el-form-item
          label="授课老师"
          :label-width="formLabelWidth"
          prop="tname"
        >
          <el-input
            v-model="FindCource.tname"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item> -->
        <el-form-item
          label="课程学分"
          :label-width="formLabelWidth"
          prop="grade"
        >
          <el-input
            v-model="FindCource.grade"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="课程类型"
          :label-width="formLabelWidth"
          prop="type"
        >
          <el-radio-group v-model="FindCource.type">
            <el-radio label="必修"></el-radio>
            <el-radio label="选修"></el-radio
          ></el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
  
  <script>
import request from "@/utils/request";
import { useListeners } from "vue";
export default {
  data() {
    return {
      cName: "",
      grade: "",
      type: "",
      tname:"",
      term:"",
      teacher:[],
      cource:[],
      pageNum: 1, //当前页码
      pageSize: 4, //每页显示的个数
      total: 0,
      dialogFormVisible: false,
      formLabelWidth: "120px",
      options: [
        // {
        //   value: " ",
        //   label: "必修选修",
        // },
        {
          value: "必修",
          label: "必修",
        },
        {
          value: "选修",
          label: "选修",
        },
      ],
      FindCource: {
        id: "",
        cName: "",
        tname:"",
        type: "",
        studentId:"",
        grade: "",
      },
      CourceForm: [
        {
          id: "",
          cName: "",
          tname:"",
          type: "",
          grade: "",
        },
      ],
      ids: [],
      user: JSON.parse(localStorage.getItem("honey-user") || "{}"),
    };
  },
  created() {
    this.load();
    this.getteacher();
    this.getcource();
  },
  // mounted() {
  // axios.get("http://localhost:9091/Student/selectAll").then(res =>{
  //     // 显示数据到后台
  //     console.log(res.data)
  //     this.tableData =res.data.data
  // })
  //   request.get("/cource/selectALL").then((res) => {
  //     this.CourceForm = res.data;
  //   });
  // },
  methods: {
    getcource(){
      this.$request.get("/cource/selectALL").then((res)=>{
        if (res.code === "200"){
          this.cource=res.data
        }
        else{
          this.$message.error(res.msg)
        }
      })
    },
    getteacher(){
      this.$request.get("/teacher/selectALL").then((res)=>{
        if (res.code === "200"){
          this.teacher=res.data
        }
        else{
          this.$message.error(res.msg)
        }
      })
    },
    del(id) {
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request.delete("/cource/delete/" + id).then((res) => {
            if (res.code === "200") {
              this.$message.success("删除成功！");
              this.load(1);
            } else {
              this.$message.error(res.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    delbatch() {
      if (!this.ids.length) {
        this.$message.warning("请选择批量删除的数据");
        return;
      }
      this.$confirm("此操作将批量删除这些数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request
            .delete("/cource/delete/batch", {
              data: this.ids,
            })
            .then((res) => {
              if (res.code === "200") {
                this.$message.success("删除成功！");
                this.load(1);
              } else {
                this.$message.error(res.msg);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 获取表格数据
    handleSelectionChange(rows) {
      // 当前选中的所有的行数据
      this.ids = rows.map((v) => v.id);
    },
    handleEdit(row) {
      //编辑数据
      this.FindCource = JSON.parse(JSON.stringify(row));
      this.dialogFormVisible = true;
    },
    handleadd() {
      //新增数据
      this.FindCource = {};
      this.dialogFormVisible = true;
    },
    save() {
      // this.$refs["reform"].validate((valid) => {
      //   if (valid) {
      if(this.user.auth==='学生'){
        this.FindCource.studentId=this.user.username;
        this.$request.post("/select/add1",this.FindCource).then((res)=>{
          if (res.code === "200") {
          this.$message.success("选课成功！");
          this.load(1);
          this.dialogFormVisible = false;
        } else {
          this.$message.error(res.msg);
        }
        }
        )
      }
      else{
      this.$request({
        url: this.FindCource.id ? "/cource/update" : "/cource/add",
        method: this.FindCource.id ? "PUT" : "POST",
        data: this.FindCource,
      }).then((res) => {
        if (res.code === "200") {
          this.$message.success("保存成功");
          this.load(1);
          this.dialogFormVisible = false;
        } else {
          this.$message.error(res.msg);
          //   }
          // });
        }
      });
    }
    },
    reset() {
      (this.cName = ""), (this.grade = ""), (this.type = ""), this.load();
    },
    load(pageNum) {
      //分页查询
      if (pageNum) {
        this.pageNum = pageNum;
      }
      if(this.user.auth==='学生'){
        this.$request
        .get("/lesson/selectByPage", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            cName: this.cName,
            tName:this.tname,
            term: this.term,
            type: this.type,
          },
        })
        .then((res) => {
          this.CourceForm = res.data.records;
          this.total = res.data.total;
        });
      }
      else{
      this.$request
        .get("/cource/selectByPage", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            cName: this.cName,
            grade: this.grade,
            type: this.type,
          },
        })
        .then((res) => {
          this.CourceForm = res.data.records;
          this.total = res.data.total;
        });
      }
    },
    handleCurrentChange(pageNum) {
      // this.pageNum = pageNum;
      this.load(pageNum);
    },
  },
};
</script>
  
<style scoped>
