<template>
  <div>
    <!-- prop：用于表单校验 -->
    <el-card style="width:500px">
      <el-form :model="userForm" label-width="80px" style="padding-right: 20px;">
        <el-form-item label="学号" prop="id">
          <el-input v-model="userForm.id" 
          :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name" v-if="user.auth==='学生'">
            <el-input v-model="userForm.name"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="tname" v-if="user.auth==='教师'">
            <el-input v-model="userForm.tname" 
            :disabled="true"></el-input>
        </el-form-item>
        <!-- <el-form-item label="密码" prop="password">
          <el-input v-model="userForm.password"></el-input>
        </el-form-item> -->
        <el-form-item label="手机号" prop="phone">
          <el-input v-model="userForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
            <el-input v-model="userForm.sex"></el-input>
        </el-form-item>
        <!-- <el-form-item label="出生日期" prop="birth">
          <el-date-picker
                v-model="userForm.birth"
                type="date"
                placeholder="选择日期"
                :picker-options="time_option"
                style="width: 200px"
              >
              </el-date-picker>
            <el-input v-model="userForm.birth"></el-input>
        </el-form-item>
        <el-form-item label="入学日期" prop="endata">
            <el-input v-model="userForm.endata" disabled="ture"></el-input>
        </el-form-item> -->
        <el-form-item label="讲师" prop="pro" v-if="user.auth==='教师'">
            <el-input v-model="userForm.pro" 
            :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="薪水" prop="salary" v-if="user.auth==='教师'">
            <el-input v-model="userForm.salary" 
            :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="班级" prop="classId" v-if="user.auth==='学生'">
            <el-input v-model="userForm.classId" 
            :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="家庭地址" prop="home" v-if="user.auth==='学生'">
            <el-input v-model="userForm.home"></el-input>
        </el-form-item>
      </el-form>
      <div style="margin-left: 80%;">
        <el-button @click="update">保 存</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userForm:{
          id:"",
          name:"",
          tname:"",
          password:"",
          phone:"",
          sex:"",
          birth:"",
          endata:"",
          salary:"",
          classId:"",
          pro:"",
          collegeId:"",
          home:"",
      },
      user: JSON.parse(localStorage.getItem('honey-user') || '{}')
    }
  },
  // 页面触发后完成数据渲染
  mounted() {
    if(this.user.auth==='学生'){
    this.$request.get("/student/selectuser",{
      params: {
            username: this.user.username,
          },
    }).then((res) => {
      this.userForm = res.data;
      // this.userForm.password=this.user.password;
    });
  }
  else if(this.user.auth==='教师'){
    this.$request.get("/teacher/selectuser",{
      params: {
            username: this.user.username,
          },
    }).then((res) => {
      this.userForm = res.data;
      // this.userForm.password=this.user.password;
    });
  }
  },
  methods: {
    update() {
      // 保存当前的用户信息到数据库
      if(this.user.auth==='学生'){
      this.$request.put('/student/update', this.userForm).then(res => {
        if (res.code === '200') {
          // 成功更新
          this.$message.success('保存成功')
        } else {
          this.$message.error(res.msg)
        }
      })
    }
  if(this.user.auth==='教师'){
      this.$request.put('/teacher/update1', this.userForm).then(res => {
        if (res.code === '200') {
          // 成功更新
          this.$message.success('保存成功')
        } else {
          this.$message.error(res.msg)
        }
      })
    }
  },
  }

}
</script>

<style scoped>

</style>