<template>
  <div id="poster">
    <el-header>
      <el-row>
        <el-col :span="12"
          ><div class="grid-content bg-purple" style="text-align: left">
            <!-- <router-link to="/Home"> -->
            <strong>学生教务系统</strong>
            <!-- </router-link> -->
          </div></el-col
        >
        <el-col :span="12"
          ><div class="grid-content bg-purple-light" style="text-align: right">
            <el-button
              type="primary"
              style="background: #505458; border: none"
              @click="toRegister('ruleForm')"
              >注册</el-button
            >
            <el-button type="primary" style="background: #505458; border: none"
              >登录</el-button
            >
          </div></el-col
        >
      </el-row>
    </el-header>
    <el-main>
      <!-- 表单 -->
      <el-form
        :model="loginForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="0px"
        class="login-Form"
      >
        <h3 class="login-title">登录界面</h3>
        <!-- 表单项 -->
        <el-form-item label="" prop="username">
          <!-- 输入值 -->
          <el-input
            type="text"
            v-model="loginForm.username"
            prefix-icon="el-icon-user"
            placeholder="账号"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="loginForm.password"
            prefix-icon="el-icon-lock"
            placeholder="密码"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item prop="code">
          <div style="display: flex">
            <el-input
              placeholder="请输入验证码"
              prefix-icon="el-icon-circle-check"
              size="medium"
              style="flex: 1"
              v-model="loginForm.code"
              clearable
            ></el-input>
            <div style="flex: 1; height: 36px">
              <!-- 获取验证码的内容 -->
              <valid-code @update:value="getCode" />
            </div>
          </div>
        </el-form-item>
        <el-form-item style="text-align: center">
          <!-- @click="onSubmit"引号中是自己起的方法名@click也可以写成v-on: click-->
          <el-button
            type="primary"
            style="background: #505458; border: none"
            @click="login"
            >登  录</el-button
          >
          <el-button
            type="primary"
            style="background: #505458; border: none"
            @click="resetForm('ruleForm')"
            >重  置</el-button
          >
          <!-- <el-button type="primary" style="background: #505458;border: none;" @click="toRegister">去注册</el-button> -->
        </el-form-item>
        <div style="display: flex">
          <!--转跳方式二 @click="$router.push('/register')" -->
          <div style="flex: 1">
            还没有账号？请
            <span style="color: #7d807f; cursor: pointer" @click="toRegister"
              >注 册</span
            >
          </div>
          <!-- <div style="flex: 1; text-align: right">
            <span style="color: #7d807f; cursor: pointer">忘记密码</span>
          </div> -->
        </div>
      </el-form>
    </el-main>
  </div>
</template>
<script>
import ValidCode from "@/components/ValidCode.vue";
// import axios from 'vuetest/node_modules/axios/dist/axios'
export default {
  name: "Login",
  components: {
    ValidCode,
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    var validateCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入验证码"));
      } else if (value.toLowerCase() != this.code) {
        callback(new Error("验证码错误"));
      } else {
        callback();
      }
    };
    return {
      code: "", //组件传过来的code
      loginForm: {
        username: "", //用户输入的code
        password: "",
        code: "",
      },
      rules: {
        username: [
          { required: true, message: "请输入账号名称", trigger: "blur" },
          { min: 2, max: 9, message: "长度2-9个字符", trigger: "blur" },
        ],
        password: [{ validator: validatePass, trigger: "blur" }],
        code: [{ validator: validateCode, trigger: "blur" }],
      },
    };
  },
  methods: {
    getCode(code) {
      // this.code = code;
      // 忽略带小写问题
      this.code = code.toLowerCase();
    },
    login() {
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          // 验证通过再发请求
          this.$request.post("/user/login", this.loginForm).then((res) => {
            console.log(res);
            if (res.code === "200") {
              this.$message({
                message: "嗨!恭喜登陆成功~",
                type: "success",
              });
              localStorage.setItem("honey-user",JSON.stringify(res.data)) //存储用户数据
              // this.$router.push({ path: "/Home" });
                this.$router.push("/Home");
            } else {
              this.$message.error(res.msg);
            }
          });
        }
      });
    },
    // 重置方法
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    toRegister() {
      this.$message({
        message: "欢迎来到注册页面~",
        type: "success",
      });
      this.$router.push({ path: "/Register" });
    },
  },
};
</script>

<style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 50px;
}
#poster {
  /* 背景图像的起始位置:top、left、right、bottom、center */
  background-position: center;
  height: 100%;
  width: 100%;
  /*
    规定背景图像的尺寸
    1.length:像素直接设置宽和高
    2.percentage：百分比直接设置宽和高
    3.cover：背景图片覆盖背景区域
    4.contain：把图片扩展到最大尺寸，适应内容区域
    5.no-repeat:背景图像仅显示一次
    */
  background-size: cover;
  /* 规定元素的定位类型
    1.static：默认值
    2.absolute：绝对定位
    3.relative：相对定位，正常位置进行定位
    4.fixed：绝对定位，相对于浏览器窗口定位
    5.inherit：规定应该从父元素继承 position 属性的值*/
  position: fixed;
  /* 超出隐藏 */
  overflow: hidden;
}
body {
  /* 把背景设为图片
    background-image: url('../assets/background.png'); */
  /* 外边框、内边框 */
  margin: 0px;
  padding: 0px;
}
.login-Form {
  /* 允许为元素添加圆角边框 */
  border-radius: 15px;
  background-clip: padding-box;
  margin: 90px auto;
  width: 400px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  /* 设置所有的边框属性:大小,样式,颜色*/
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.login-title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
/* a指文字不带下划线且为白色，router-link-active指点击后变为什么颜色。 */
.router-link-active {
  text-decoration: none;
  color: rgb(6, 6, 6);
}
a {
  text-decoration: none;
  color: rgb(7, 7, 7);
}
</style>
