<template>
  <div>
    <div class="title">
      <h3>授课信息管理</h3>
    </div>

    <div class="title">
      <el-row>
        <el-col :span="6">
          <div style="text-align: left; margin-left: 50px" v-if="user.auth==='管理员'">
            <h3 style="margin-right: 10px,align:center">查询输入框</h3>
            <el-input
              placeholder="查询课程"
              v-model="cName"
              clearable
              style="width: 200px; margin: 10px 10px"
            ></el-input>
            <el-input
              placeholder="查询授课老师"
              v-model="tName"
              clearable
              style="width: 200px; margin: 10px 10px"
            ></el-input>
            <el-select
              v-model="type"
              clearable
              placeholder="课程类型"
              style="width: 200px; margin: 10px 10px"
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <!-- <el-input
              placeholder="查询课程类型"
              v-model="type"
              clearable
              style="width: 200px; margin: 10px 10px"
            ></el-input> -->
            <el-input
              placeholder="查询授课学期"
              v-model="term"
              clearable
              style="width: 200px; margin: 10px 10px"
            ></el-input>
            <div>
            <el-button
              icon="el-icon-search"
              round
              @click="load(1)"
              size="small"
              style="margin-left: 60px"
              type="primary"
              plain
              >查询</el-button
            >
            <el-button
              icon="el-icon-search"
              round
              @click="reset"
              size="small"
              style="margin: 10px 5px"
              type="primary"
              plain
              >重置</el-button
            >
            </div>
          </div>
        </el-col>

        <el-col :span="18">
          <div style="margin: 15px 60px" v-if="user.auth==='管理员'">
            <el-button
              icon="el-icon-circle-plus-outline"
              round
              size="small"
              type="primary"
              plain
              @click="handleadd"
              >新增</el-button
            >
            <el-button
              icon="el-icon-delete"
              round
              size="small"
              type="danger"
              plain
              @click="delbatch"
              >批量删除</el-button
            >
          </div>
          <div style="height: 480px">
            <el-table
              :data="FingTeachingForm"
              style="
                width: 98%;
                height: 250;
                margin-top: 15px;
              "
              :header-cell-style="{
                backgroundColor: 'aliceblue',
                color: '#666',
              }"
              @selection-change="handleSelectionChange"
            >
              <el-table-column
                type="selection"
                width="55"
                align="center"
              ></el-table-column>
              <el-table-column
                prop="cname"
                label="课程名称"
              ></el-table-column>
              <el-table-column
                prop="type"
                label="课程类型"
              ></el-table-column>
              <el-table-column
                prop="grade"
                label="课程学分"
              ></el-table-column>
              <el-table-column
                prop="tname"
                label="授课教师"
              ></el-table-column>
              <el-table-column
                prop="clName"
                label="授课班级"
              ></el-table-column>
              <el-table-column
                prop="term"
                label="授课学期"
              ></el-table-column>
              <el-table-column
                label="操作"
                align="center"
                width="150"
                v-slot="scope"
                v-if="user.auth==='管理员'"
              >
                <el-button
                  type="primary"
                  size="small"
                  @click="handleEdit(scope.row)"
                  >编辑</el-button
                >
                <el-button type="danger" size="small" @click="del(scope.row.id)"
                  >删除</el-button
                >
              </el-table-column>
            </el-table>

            <div style="margin: 10px 50px">
              <el-pagination
                @current-change="handleCurrentChange"
                :current-page="pageNum"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="pageSize"
                layout="total,prev, pager, next"
                :total="total"
              >
              </el-pagination>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      title="授课信息"
      :visible.sync="dialogFormVisible"
      style="
        margin-top: 20px;
        margin-left: 400px;
        width: 800px;
        overflow: hidden;
      "
    >
      <el-form :model="FingTeaching" class="Dialog" style="width: 200px" >
        <!-- <el-form-item label="课程编号" :label-width="formLabelWidth" prop="id"> -->
        <!-- <el-input v-model="FindCource.id" style="width: 200px"></el-input> -->
        <!-- </el-form-item> -->
        <el-form-item
          label="课程名称"
          :label-width="formLabelWidth"
          prop="cname"
        >
          <el-input
            v-model="FingTeaching.cname"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="课程学分"
          :label-width="formLabelWidth"
          prop="grade"
        >
          <el-input
            v-model="FingTeaching.grade"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="课程类型"
          :label-width="formLabelWidth"
          prop="type"
        >
          <el-radio-group v-model="FingTeaching.type">
            <el-radio label="必修"></el-radio>
            <el-radio label="选修"></el-radio
          ></el-radio-group>
        </el-form-item>
        <el-form-item
          label="授课老师"
          :label-width="formLabelWidth"
          prop="tname"
        >
          <el-input
            v-model="FingTeaching.tname"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="授课班级"
          :label-width="formLabelWidth"
          prop="classroomId"
        >
          <el-select v-model="FingTeaching.classroomId" style="width: 200px">
              <el-option
                v-for="item in classroom"
                :key="item.id"  
                :label="item.clName"
                :value="item.id"
              >
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item
          label="授课学期"
          :label-width="formLabelWidth"
          prop="grade"
        >
          <el-input
            v-model="FingTeaching.term"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
  
  <script>
import request from "@/utils/request";
export default {
  data() {
    return {
      cName: "",
      type: "",
      tName: "",
      term: "",
      pageNum: 1, //当前页码
      pageSize: 10, //每页显示的个数
      total: 0,
      dialogFormVisible: false,
      formLabelWidth: "120px",
      ids: [],
      classroom:[],
      options: [
        // {
        //   value: " ",
        //   label: "必修选修",
        // },
        {
          value: "必修",
          label: "必修",
        },
        {
          value: "选修",
          label: "选修",
        },
      ],
      FingTeaching: {
        id: "",
        cname: "",
        type: "",
        grade: "",
        tname: "",
        term: "",
        classroomId:"",
        clName:""
      },
      FingTeachingForm: [
        {
          id: "",
          cname: "",
          type: "",
          grade: "",
          tname: "",
          term: "",
          classroomId:"",
          clName:""
        },
      ],
      user: JSON.parse(localStorage.getItem("honey-user") || "{}"),
    };
  },
  // mounted() {
  // axios.get("http://localhost:9091/Student/selectAll").then(res =>{
  //     // 显示数据到后台
  //     console.log(res.data)
  //     this.tableData =res.data.data
  // })
  // request.get("/lesson/selectALL").then((res) => {
  //     this.FingTeachingForm = res.data
  //   });
  // },
  created() {
    this.load();
    this.getclassroom();
  },
  methods: {
    getclassroom(){
      this.$request.get("/class/selectALL").then((res)=>{
        if (res.code === "200"){
          this.classroom=res.data
        }
        else{
          this.$message.error(res.msg)
        }
      })
    },
    del(id) {
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request.delete("/lesson/delete/" + id).then((res) => {
            if (res.code === "200") {
              this.$message.success("删除成功！");
              this.load(1);
            } else {
              this.$message.error(res.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    delbatch() {
      if (!this.ids.length) {
        this.$message.warning("请选择批量删除的数据");
        return;
      }
      this.$confirm("此操作将批量删除这些数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request
            .delete("/lesson/delete/batch", {
              data: this.ids,
            })
            .then((res) => {
              if (res.code === "200") {
                this.$message.success("删除成功！");
                this.load(1);
              } else {
                this.$message.error(res.msg);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 获取表格数据
    handleSelectionChange(rows) {
      // 当前选中的所有的行数据
      this.ids = rows.map((v) => v.id);
    },
    handleEdit(row) {
      //编辑数据
      this.FingTeaching = JSON.parse(JSON.stringify(row));
      this.dialogFormVisible = true;
    },
    handleadd() {
      //新增数据
      this.FingTeaching = {};
      this.dialogFormVisible = true;
    },
    save() {
      // this.$refs["reform"].validate((valid) => {
      //   if (valid) {
      this.$request({
        url: this.FingTeaching.id ? "/lesson/update" : "/lesson/add",
        method: this.FingTeaching.id ? "PUT" : "POST",
        data: this.FingTeaching,
      }).then((res) => {
        if (res.code === "200") {
          this.$message.success("保存成功");
          this.load(1);
          this.dialogFormVisible = false;
        } else {
          this.$message.error(res.msg);
        }
      });
      //   }
      // });
    },
    reset() {
      (this.cName = ""),
        (this.type = ""),
        (this.tName = ""),
        (this.term = ""),
        this.load();
    },
    load(pageNum) {
      //分页查询
      if (pageNum) {
        this.pageNum = pageNum;
      }
      this.$request
        .get("/lesson/selectByPage", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            cName: this.cName,
            tName: this.tName,
            type: this.type,
            term: this.term,
          },
        })
        .then((res) => {
          this.FingTeachingForm = res.data.records;
          this.total = res.data.total;
        });
    },
    handleCurrentChange(pageNum) {
      // this.pageNum = pageNum;
      this.load(pageNum);
    },
  },
};
</script>
  
  <style>
.score-Form {
  width: 70%;
  height: 20%;
}
</style>