<template>
  <div>
    <div class="title">
      <h3>学院管理</h3>
    </div>

    <div class="title">
      <el-row style="margin-top: 5px">
        <el-col :span="6">
          <div style="text-align: left; margin-left: 50px">
            <h3 style="margin-right: 10px,align:center">查询输入框</h3>
            <el-input
              placeholder="查询学院名称"
              v-model="collegename"
              clearable
              style="width: 200px; margin: 10px 10px"
            ></el-input>
            <el-input
              placeholder="查询院系主任"
              v-model="collegeteacher"
              clearable
              style="width: 200px; margin: 10px 10px"
            ></el-input>
            <div>
            <el-button
              icon="el-icon-search"
              round
              @click="load(1)"
              size="small"
              style="margin-left: 60px"
              type="primary"
              plain
              >查询</el-button
            >
            <el-button
              icon="el-icon-search"
              round
              @click="reset"
              size="small"
              style="margin: 10px 5px"
              type="primary"
              plain
              >重置</el-button
            >
            </div>
          </div>
        </el-col>

        <el-col :span="16" style="margin-top: 0px">
          <div>
            <div style="margin-top: 50px; margin: 10px 100px">
              <el-button
                icon="el-icon-circle-plus-outline"
                round
                size="small"
                type="primary"
                plain
                @click="handleadd"
                >新增</el-button
              >
              <el-button
                icon="el-icon-delete"
                round
                size="small"
                type="danger"
                plain
                @click="delbatch"
                >批量删除</el-button
              >
            </div>
            <!-- :header-cell-style表头样式 -->
            <el-table
              :data="collegeForm"
              style="width: 95%; margin-left: 100px"
              height="350"
              :header-cell-style="{
                backgroundColor: 'aliceblue',
                color: '#666',
              }"
              @selection-change="handleSelectionChange"
            >
              <el-table-column type="selection" width="55" align="center">
              </el-table-column>
              <!-- <el-table-column
            prop="id"
            label="学院编号"
            width="150"
          ></el-table-column> -->
              <el-table-column
                prop="name"
                label="学院名称"
                width="300"
              ></el-table-column>
              <el-table-column
                prop="teacher"
                label="院系主任"
                width="200"
              ></el-table-column>
              <el-table-column
                label="操作"
                width="200"
                align="center"
                v-slot="scope"
              >
                <el-button
                  type="primary"
                  size="small"
                  @click="handleEdit(scope.row)"
                  >编辑</el-button
                >
                <el-button type="danger" size="small" @click="del(scope.row.id)"
                  >删除</el-button
                >
              </el-table-column>
            </el-table>

            <div style="margin: 10px 100px">
              <el-pagination
                @current-change="handleCurrentChange"
                :current-page="pageNum"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="pageSize"
                layout="total,prev, pager, next"
                :total="total"
              >
              </el-pagination>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      title="学院信息"
      :visible.sync="dialogFormVisible"
      style="margin-top: 100px; margin-left: 300px"
      class="DialogFrom"
    >
      <el-form :model="addcollege" class="Dialog" :rules="rules" ref="reform">
        <el-form-item
          label="学院名称"
          :label-width="formLabelWidth"
          prop="name"
        >
          <el-input v-model="addcollege.name" ></el-input>
        </el-form-item>
        <el-form-item
          label="院系主任"
          :label-width="formLabelWidth"
          prop="teacher"
        >
          <el-input
            v-model="addcollege.teacher"
            autocomplete="off"
            width="60px"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import request from "@/utils/request";
export default {
  data() {
    return {
      collegename: "",
      collegeteacher: "",
      pageNum: 1, //当前页码
      pageSize: 5, //每页显示的个数
      total: 0,
      dialogFormVisible: false,
      formLabelWidth: "120px",
      collegeForm: [
        {
          id: "",
          name: "",
          teacher: "",
        },
      ],
      addcollege: {
        id: "",
        name: "",
        teacher: "",
      },
      rules: {
        name: [{ request: true, message: "请输入学院名称", trigger: "blur" }],
        teacher: [
          { request: true, message: "请输入院系主任名称", trigger: "blur" },
        ],
      },
      ids:[]
    };
  },
  // 页面触发后完成数据渲染
  // mounted() {
  // axios.get("http://localhost:9091/Student/selectAll").then(res =>{
  //     // 显示数据到后台
  //     console.log(res.data)
  //     this.tableData =res.data.data
  // })
  //   request.get("/college//selectALL").then((res) => {
  //     this.collegeForm = res.data;
  //   });
  // },
  created() {
    this.load();
  },
  methods: {
    del(id) {
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request.delete("/college/delete/"+id).then(res=>{
            if (res.code === "200") {
              this.$message.success("删除成功！");
              this.load(1);
            }else{
              this.$message.error(res.msg)
            }
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    delbatch(){
      if (!this.ids.length) {
        this.$message.warning('请选择批量删除的数据')
        return
      }
      this.$confirm("此操作将批量删除这些数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request.delete("/college/delete/batch",{
            data:this.ids
          }).then(res=>{
            if (res.code === "200") {
              this.$message.success("删除成功！");
              this.load(1);
            }else{
              this.$message.error(res.msg)
            }
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 获取表格数据
    handleSelectionChange(rows){ // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    handleEdit(row) {
      //编辑数据
      this.addcollege = JSON.parse(JSON.stringify(row));
      this.dialogFormVisible = true;
    },
    handleadd() {
      //新增数据
      this.addcollege = {};
      this.dialogFormVisible = true;
    },
    save() {
      this.$refs["reform"].validate((valid) => {
        if (valid) {
          this.$request({
            url: this.addcollege.id ? "/college/update" : "/college/add",
            method: this.addcollege.id ? "PUT" : "POST",
            data: this.addcollege,
          }).then((res) => {
            if (res.code === "200") {
              this.$message.success("保存成功");
              this.load(1);
              this.dialogFormVisible = false;
            } else {
              this.$message.error(res.msg);
            }
          });
        }
      });
    },
    reset() {
      this.collegename = ""
      this.collegeteacher = ""
      this.load();
    },
    load(pageNum) {
      //分页查询
      if (pageNum) {
        this.pageNum = pageNum;
      }
      this.$request
        .get("/college/selectByPage", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            name: this.collegename,
            teacher: this.collegeteacher,
          },
        })
        .then((res) => {
          this.collegeForm = res.data.records;
          this.total = res.data.total;
        });
    },
    handleCurrentChange(pageNum) {
      // this.pageNum = pageNum;
      this.load(pageNum);
    }
  },
};
</script>

<style scoped>
.el-form-item {
  text-align: center;
  width: 400px;
  margin: 5px;
}
.Dialog {
  text-align: center;
  width: 400px;
  height: auto;
}

.DialogFrom {
  text-align: center;
  width: 1000px;
  height: auto;
  overflow: hidden;
  margin-left: 200px;
}
</style>
