<template>
  <div>
    <div class="title">
      <h3>用户管理</h3>
    </div>
    <!-- 
    <div class="title">
      <el-row style="margin-top: 10px">
        <el-col :span="6"
          ><div class="poster">
            <div class="select">
              <el-form
                :model="AddUser"
                status-icon
                ref="ruleForm"
                label-width="100px"
                class="classForm"
              >
                <h4>添加用户</h4>
                <el-form-item label="账号：">
                  <el-input type="text" v-model="AddUser.name"></el-input>
                </el-form-item>
                <el-form-item label="密码：">
                  <el-input
                    type="password"
                    v-model="AddUser.password"
                  ></el-input>
                </el-form-item>
                <el-form-item label="用户类型">
                  <el-radio-group v-model="AddUser.auth">
                    <el-radio label="管理员"></el-radio>
                    <el-radio label="教师"></el-radio>
                    <el-radio label="学生"></el-radio>
                  </el-radio-group>
                </el-form-item>
                <el-form-item>
                  <el-button
                    type="primary"
                    style="background: #505458; border: none"
                    >确定</el-button
                  >
                  <el-button
                    type="primary"
                    style="background: #505458; border: none"
                    >取消</el-button
                  >
                </el-form-item>
              </el-form>
            </div>
          </div></el-col
        >
        <el-col :span="18" style="margin-top: 10px"
          > -->
      <div style="text-align: left; margin: 10px">
        <el-input
          placeholder="请输入用户名"
          v-model="username"
          clearable
          style="width: 180px; margin-right: 10px;"
          size="small"
        ></el-input>
        <!-- <el-input
          placeholder="请输入用户类型"
          v-model="auth"
          clearable
          style="width: 200px; margin-right: 10px"
          size="small"
        ></el-input> -->
        <el-select v-model="auth" style="width: 180px;margin-right: 10px" placeholder="请输入用户类型" size="small">
          <el-option value="管理员" label="管理员"></el-option>
          <el-option value="教师" label="教师"></el-option>
          <el-option value="学生" label="学生" ></el-option>
        </el-select>
        <el-button icon="el-icon-search" round size="small" @click="load(1)">查询</el-button>
        <el-button icon="el-icon-search" round  size="small" @click="reset">重置</el-button>
      </div>
    <div style="margin: 10px 20px">
      <el-button
        icon="el-icon-circle-plus-outline"
        round
        size="small"
        type="primary"
        plain
        @click="handleadd"
        >新增</el-button
      >
      <el-button
        icon="el-icon-delete"
        round
        size="small"
        type="danger"
        plain
        @click="delbatch"
        >批量删除</el-button
      >
    </div>
    <div class="poster">
      <div>
        <el-table
          :data="UserForm"
          style="width: 80%;"          
          :header-cell-style="{
            backgroundColor: 'aliceblue',
            color: '#666',
          }"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" align="center"></el-table-column>
          <el-table-column prop="username" label="用户账号"></el-table-column>
          <el-table-column prop="password" label="用户密码"></el-table-column>
          <el-table-column prop="auth" label="用户权限"></el-table-column>
          <!-- <el-table-column prop="url" label="头像"></el-table-column> -->
          <el-table-column label="操作" v-slot="scope" align="center">
            <el-button
              type="primary"
              size="small"
              @click="handleEdit(scope.row)"
              >编辑</el-button
            >
            <el-button type="danger" size="small" @click="del(scope.row.id)"
              >删除</el-button
            >
          </el-table-column>
        </el-table>
      </div>
      <div style="margin: 10px 0px">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pageSize"
          layout="total,prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </div>
      <!-- </div>
            </el-col
      ></el-row> -->
    </div>
    <el-dialog
      title="用户信息"
      :visible.sync="dialogFormVisible"
      style="
        margin-top: 20px;
        margin-left: 400px;
        width: 800px;
        overflow: hidden;
      "
    >
      <el-form :model="AddUser" class="Dialog" style="width: 200px">
        <!-- <el-form-item label="课程编号" :label-width="formLabelWidth" prop="id"> -->
        <!-- <el-input v-model="FindCource.id" style="width: 200px"></el-input> -->
        <!-- </el-form-item> -->
        <el-form-item
          label="用户账号"
          :label-width="formLabelWidth"
          prop="username"
        >
          <el-input
            v-model="AddUser.username"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="用户名称"
          :label-width="formLabelWidth"
          prop="name"
          v-if="AddUser.auth!='管理员'"
        >
          <el-input
            v-model="AddUser.name"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="用户密码"
          :label-width="formLabelWidth"
          prop="password"
        >
          <el-input
            v-model="AddUser.password"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="用户权限"
          :label-width="formLabelWidth"
          prop="auth"
        >
          <el-select v-model="AddUser.auth" style="width: 200px" >
          <el-option value="管理员" label="管理员"></el-option>
          <el-option value="教师" label="教师"></el-option>
          <el-option value="学生" label="学生" ></el-option>
        </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
  
  <script>
import request from "@/utils/request";
export default {
  data() {
    return {
      username:"",
      auth:"",
      pageNum: 1, //当前页码
      pageSize: 6, //每页显示的个数
      total: 0,
      ids: [],
      dialogFormVisible:false,
      formLabelWidth: "120px",
      UserForm: [
        {
          username: "",
          password: "",
          auth: "",
          url: "",
        },
      ],
      AddUser: {
        username: "",
        password: "",
        auth: "",
        name:"",
      },
    };
  },
  // 页面触发后完成数据渲染
  // mounted() {
    // axios.get("http://localhost:9091/Student/selectAll").then(res =>{
    //     // 显示数据到后台
    //     console.log(res.data)
    //     this.tableData =res.data.data
    // })
  //   request.get("/user/selectALL").then((res) => {
  //     this.UserForm = res.data;
  //   });
  // },
  created() {
    this.load();
  },
  methods: {
    del(id) {
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request.delete("/user/delete/" + id).then((res) => {
            if (res.code === "200") {
              this.$message.success("删除成功！");
              this.load(1);
            } else {
              this.$message.error(res.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    delbatch() {
      if (!this.ids.length) {
        this.$message.warning("请选择批量删除的数据");
        return;
      }
      this.$confirm("此操作将批量删除这些数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request
            .delete("/user/delete/batch", {
              data: this.ids,
            })
            .then((res) => {
              if (res.code === "200") {
                this.$message.success("删除成功！");
                this.load(1);
              } else {
                this.$message.error(res.msg);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 获取表格数据
    handleSelectionChange(rows) {
      // 当前选中的所有的行数据
      this.ids = rows.map((v) => v.id);
    },
    handleEdit(row) {
      //编辑数据
      this.AddUser = JSON.parse(JSON.stringify(row));
      if(this.AddUser.auth=== "学生"){
          this.$request.get("/student/selectuser",{
        params: {
              username: this.AddUser.username,
            },
      }).then((res) => {
        this.AddUser.name = res.data.name;
      });
      }
      else if(this.AddUser.auth=== "教师"){
          this.$request.get("/teacher/selectuser",{
        params: {
              username: this.AddUser.username,
            },
      }).then((res) => {
        this.AddUser.name = res.data.name;
      });
      }
      this.dialogFormVisible = true;
    },
    handleadd() {
      //新增数据
      this.AddUser = {};
      this.dialogFormVisible = true;
    },
    save() {
      // this.$refs["reform"].validate((valid) => {
      //   if (valid) {
      this.$request({
        url: this.AddUser.id ? "/user/update" : "/user/add",
        method: this.AddUser.id ? "PUT" : "POST",
        data: this.AddUser,
      }).then((res) => {
        if (res.code === "200") {
          this.$message.success("保存成功");
          this.load(1);
          this.dialogFormVisible = false;
        } else {
          this.$message.error(res.msg);
        }
        //   });
        // }
      });
    },
    reset() {
      this.username = "";
      this.auth = "";
      this.load();
    },
    load(pageNum) {
      //分页查询
      if (pageNum) {
        this.pageNum = pageNum;
      }
      this.$request
        .get("/user/selectByPage", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            username:this.username,
            auth:this.auth,
          },
        })
        .then((res) => {
          this.UserForm = res.data.records;
          this.total = res.data.total;
        });
    },
    handleCurrentChange(pageNum) {
      // this.pageNum = pageNum;
      this.load(pageNum);
    },
  },
};
</script>
  
  <style>
.score-Form {
  width: 70%;
  height: 20%;
}
.poster {
  border: 1px;
}
</style>