<template>
  <div>
    <div class="title">
      <h3>学生成绩管理</h3>
    </div>

    <div class="title">
      <el-row>
        <el-col :span="6">
          <div style="text-align: left; margin-left: 50px" v-if="user.auth==='管理员'||user.auth==='教师'" >
            <h3 style="margin-right: 10px,align:center">查询输入框</h3>
            <el-input
              placeholder="查询学生学号"
              v-model="id"
              clearable
              style="width: 200px; margin: 10px 10px"
            ></el-input>
            <el-input
              placeholder="查询学生姓名"
              v-model="name"
              clearable
              style="width: 200px; margin: 10px 10px"
            ></el-input>
            <el-input
              placeholder="查询课程名称"
              v-model="cName"
              clearable
              style="width: 200px; margin: 10px 10px"
            ></el-input>
            <el-input
              placeholder="查询课程成绩"
              v-model="score"
              clearable
              style="width: 200px; margin: 10px 10px"
            ></el-input>
            <div>
            <el-button
              icon="el-icon-search"
              round
              @click="load(1)"
              size="small"
              style="margin-left: 60px"
              type="primary"
              plain
              >查询</el-button
            >
            <el-button
              icon="el-icon-search"
              round
              @click="reset"
              size="small"
              style="margin: 10px 5px"
              type="primary"
              plain
              >重置</el-button
            >
            </div>
          </div>
        </el-col>

        <el-col :span="18">
          <div style="margin: 15px 60px"  v-if="user.auth==='管理员'||user.auth==='教师'">
            <el-button
              icon="el-icon-circle-plus-outline"
              round
              size="small"
              type="primary"
              plain
              @click="handleadd"
              >新增</el-button
            >
            <el-button
              icon="el-icon-delete"
              round
              size="small"
              type="danger"
              plain
              @click="delbatch"
              >批量删除</el-button
            >
          </div>
          <div style="height: 480px">
            <el-table
              :data="tableData"
              style="width: 95%; height=: 250; margin-left: 30px"
              :header-cell-style="{
                backgroundColor: 'aliceblue',
                color: '#666',
              }"
              @selection-change="handleSelectionChange"
            >
              <el-table-column
                type="selection"
                width="55"
                align="center"
              ></el-table-column>
              <el-table-column
                prop="sid"
                label="序号"
              ></el-table-column>
              <el-table-column
                prop="cname"
                label="课程名称"
              ></el-table-column>
              <el-table-column
                prop="tname"
                label="授课老师"
              ></el-table-column>
              <el-table-column
                prop="id"
                label="学生学号"
              ></el-table-column>
              <el-table-column
                prop="name"
                label="学生姓名"
              ></el-table-column>
              <el-table-column
                prop="score"
                label="考试成绩"
              ></el-table-column>
              <el-table-column
                label="操作"
                width="180"
                align="center"
                v-slot="scope"
                v-if="user.auth==='管理员'||user.auth==='教师'"
              >
                <el-button
                  type="primary"
                  size="small"
                  @click="handleEdit(scope.row)"
                  >编辑</el-button
                >
                <el-button type="danger" size="small" @click="del(scope.row.id)"
                  >删除</el-button
                >
              </el-table-column>
            </el-table>

            <div style="margin: 10px 30px">
              <el-pagination
                @current-change="handleCurrentChange"
                :current-page="pageNum"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="pageSize"
                layout="total,prev, pager, next"
                :total="total"
              >
              </el-pagination>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      title="选课信息"
      :visible.sync="dialogFormVisible"
      style="
        margin-top: 20px;
        margin-left: 400px;
        width: 800px;
        overflow: hidden;
      "
      class="DialogFrom"
    >
      <el-form :model="Selectscore" class="Dialog" style="width: 200px">
        <el-form-item
          label="课程名称"
          :label-width="formLabelWidth"
          prop="cname"
        >
          <el-input
            v-model="Selectscore.cname"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="授课老师"
          :label-width="formLabelWidth"
          prop="tname"
        >
          <el-input
            v-model="Selectscore.tname"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item label="学生学号" :label-width="formLabelWidth" prop="id">
          <el-input
            v-model="Selectscore.id"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="学生姓名"
          :label-width="formLabelWidth"
          prop="name"
        >
          <el-input
            v-model="Selectscore.name"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="考试成绩"
          :label-width="formLabelWidth"
          prop="score"
        >
          <el-input
            v-model="Selectscore.score"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: JSON.parse(localStorage.getItem("honey-user") || "{}"),
      id: "",
      cName: "",
      name: "",
      teacherId:"",
      score: "",
      pageNum: 1, //当前页码
      pageSize: 10, //每页显示的个数
      total: 0,
      dialogFormVisible: false,
      formLabelWidth: "120px",
      ids: [],
      tableData: [
        {
          sid: "",
          cname: "",
          teacherId:"",
          tname:"",
          id: "",
          name: "",
          score: "",
        },
      ],
      Selectscore: {
        sid: "",
        teacherId:"",
        tname:"",
        cname: "",
        id: "",
        name: "",
        score: "",
      },
    };
  },
  created() {
    this.load();
  },
  methods: {
    del(id) {
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request.delete("/select/delete/" + id).then((res) => {
            if (res.code === "200") {
              this.$message.success("删除成功！");
              this.load(1);
            } else {
              this.$message.error(res.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },

    delbatch() {
      if (!this.ids.length) {
        this.$message.warning("请选择批量删除的数据");
        return;
      }
      this.$confirm("此操作将批量删除这些数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request
            .delete("/select/delete/batch", {
              data: this.ids,
            })
            .then((res) => {
              if (res.code === "200") {
                this.$message.success("删除成功！");
                this.load(1);
              } else {
                this.$message.error(res.msg);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 获取表格数据
    handleSelectionChange(rows) {
      // 当前选中的所有的行数据
      this.ids = rows.map((v) => v.sid);
    },
    handleEdit(row) {
      //编辑数据
      this.Selectscore = JSON.parse(JSON.stringify(row));
      this.dialogFormVisible = true;
    },
    handleadd() {
      //新增数据
      this.Selectscore = {};
      this.dialogFormVisible = true;
    },
    save() {
      // this.$refs["reform"].validate((valid) => {
      //   if (valid) {
      this.$request({
        url: this.Selectscore.sid ? "/select/update" : "/select/add",
        method: this.Selectscore.sid ? "PUT" : "POST",
        data: this.Selectscore,
      }).then((res) => {
        if (res.code === "200") {
          this.$message.success("保存成功");
          this.load(1);
          this.dialogFormVisible = false;
        } else {
          this.$message.error(res.msg);
        }
      });
      //   }
      // });
    },
    reset() {
      (this.name = ""),
        (this.score = ""),
        (this.id = ""),
        (this.cName = ""),
        this.load();
    },
    load(pageNum) {
      //分页查询
      if (pageNum) {
        this.pageNum = pageNum;
      }
      this.$request
        .get("/select/selectByPage", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            cName: this.cName,
            teacherId:this.teacherId,
            name: this.name,
            id: this.id,
            score: this.score,
          },
        })
        .then((res) => {
          this.tableData = res.data.records;
          this.total = res.data.total;
        });
    },
    handleCurrentChange(pageNum) {
      // this.pageNum = pageNum;
      this.load(pageNum);
    },
  },
};
</script>

<style>
.score-Form {
  width: 70%;
  height: 20%;
}
</style>