<template>
  <div>
    <div class="title">
      <h3>学生管理</h3>
    </div>
    <div style="margin-top: 20px;">
        <el-table
              :data="tableData"
              style="width: 95%; height=: 250; margin-left: 30px"
              :header-cell-style="{
                backgroundColor: 'aliceblue',
                color: '#666',
              }"
              @selection-change="handleSelectionChange"
            >
              <el-table-column
                type="selection"
                width="55"
                align="center"
              ></el-table-column>
              <!-- <el-table-column
                prop="sid"
                label="序号"
              ></el-table-column> -->
              <el-table-column
                prop="cname"
                label="课程名称"
              ></el-table-column>
              <el-table-column
                prop="id"
                label="学生学号"
              ></el-table-column>
              <el-table-column
                prop="name"
                label="学生姓名"
              ></el-table-column>
              <el-table-column
                prop="score"
                label="考试成绩"
              ></el-table-column>
              <el-table-column
                label="操作"
                width="180"
                align="center"
                v-slot="scope"
              >
                <el-button
                  type="primary"
                  size="small"
                  @click="handleEdit(scope.row)"
                  >编辑</el-button
                >
              </el-table-column>
            </el-table>
            <div style="margin: 10px 30px">
              <el-pagination
                @current-change="handleCurrentChange"
                :current-page="pageNum"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="pageSize"
                layout="total,prev, pager, next"
                :total="total"
              >
              </el-pagination>
            </div>
    </div>
    <el-dialog
      title="选课信息"
      :visible.sync="dialogFormVisible"
      style="
        margin-top: 20px;
        margin-left: 400px;
        width: 800px;
        overflow: hidden;
      "
      class="DialogFrom"
    >
      <el-form :model="Selectscore" class="Dialog" style="width: 200px">
        <el-form-item
          label="课程名称"
          :label-width="formLabelWidth"
          prop="cname"
        >
          <el-input
            v-model="Selectscore.cname"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item label="学生学号" :label-width="formLabelWidth" prop="id">
          <el-input
            v-model="Selectscore.id"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="学生姓名"
          :label-width="formLabelWidth"
          prop="name"
        >
          <el-input
            v-model="Selectscore.name"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="考试成绩"
          :label-width="formLabelWidth"
          prop="score"
        >
          <el-input
            v-model="Selectscore.score"
            autocomplete="off"
            width="60px"
            style="width: 200px"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="save">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
    data() {
    return {
      user: JSON.parse(localStorage.getItem("honey-user") || "{}"),
      id: "",
      cName: "",
      name: "",
      score: "",
      pageNum: 1, //当前页码
      pageSize: 10, //每页显示的个数
      total: 0,
      dialogFormVisible: false,
      formLabelWidth: "120px",
      ids: [],
      tableData: [
        {
          sid: "",
          cname: "",
          id: "",
          name: "",
          score: "",
        },
      ],
      Selectscore: {
        sid: "",
        cname: "",
        id: "",
        name: "",
        score: "",
      },
    };
  },
  created() {
    this.load();
  },
  methods: {
    // 获取表格数据
    handleSelectionChange(rows) {
      // 当前选中的所有的行数据
      this.ids = rows.map((v) => v.sid);
    },
    handleEdit(row) {
      //编辑数据
      this.Selectscore = JSON.parse(JSON.stringify(row));
      this.dialogFormVisible = true;
    },
    //更新未完成
    save() {
        this.$request.put('/select/update1',this.Selectscore).then(res => {
        if (res.code === '200') {
          // 成功更新
          this.$message.success("保存成功");
          this.load(1);
          this.dialogFormVisible = false;
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    load(pageNum) {
      //分页查询
      if (pageNum) {
        this.pageNum = pageNum;
      }
      this.$request
        .get("/select/selectByPage", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            cName: this.cName,
            teacherId:this.user.username,
            name: this.name,
            id: this.id,
            score: this.score,
          },
        })
        .then((res) => {
          this.tableData = res.data.records;
          this.total = res.data.total;
        });
    },
    handleCurrentChange(pageNum) {
      // this.pageNum = pageNum;
      this.load(pageNum);
    },
  },
}
</script>

