<template>
  <div>
    <div class="title">
      <h3>学生管理</h3>
    </div>

    <div class="title">
      <div style="text-align: left; margin: 20px">
        <el-input
          placeholder="请输入查询学号"
          v-model="inputid"
          clearable
          style="width: 200px; margin-right: 10px"
        ></el-input>
        <el-input
          placeholder="请输入查询姓名"
          v-model="inputname"
          clearable
          style="width: 200px; margin-right: 10px"
        ></el-input>
        <el-input
          placeholder="请输入查询班级"
          v-model="clName"
          clearable
          style="width: 200px; margin-right: 10px"
        ></el-input>
        <el-button icon="el-icon-search" round @click="load(1)">查询</el-button>
        <el-button icon="el-icon-search" round @click="reset">重置</el-button>
      </div>

      <div style="margin: 0px 20px">
        <el-button
          icon="el-icon-circle-plus-outline"
          round
          size="small"
          type="primary"
          plain
          @click="handleadd"
          >新增</el-button
        >
        <el-button
          icon="el-icon-delete"
          round
          size="small"
          type="danger"
          plain
          @click="delbatch"
          >批量删除</el-button
        >
      </div>
      <div>
        <el-table
          :data="studentFrom"
          style="width: 95%; margin: 20px"
          height="350"
          :header-cell-style="{
            backgroundColor: 'aliceblue',
            color: '#666',
          }"
          @selection-change="handleSelectionChange"
        >
          <el-table-column
            type="selection"
            width="55"
            align="center"
          ></el-table-column>
          <el-table-column prop="id" label="学号"></el-table-column>
          <el-table-column
            prop="name"
            label="姓名"
          ></el-table-column>
          <el-table-column prop="sex" label="性别" ></el-table-column>
          <el-table-column
            prop="birth"
            label="出生日期"
          ></el-table-column>
          <el-table-column
            prop="endata"
            label="入学日期"
          ></el-table-column>
          <el-table-column
            prop="classId"
            label="班级"
          ></el-table-column>
          <el-table-column
            prop="home"
            label="家庭地址"
          ></el-table-column>
          <el-table-column
            prop="phone"
            label="手机号"
          ></el-table-column>
          <el-table-column
            prop="clName"
            label="班级"
          ></el-table-column>
          <el-table-column
            label="操作"
            width="150"
            align="center"
            v-slot="scope"
          >
            <el-button
              type="primary"
              size="small"
              @click="handleEdit(scope.row)"
              >编辑</el-button
            >
            <el-button type="danger" size="small" @click="del(scope.row.id)"
              >删除</el-button
            >
          </el-table-column>
        </el-table>
      </div>
      <div style="margin: 10px 100px">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pageSize"
          layout="total,prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </div>
      <!-- 嵌套表格 -->
      <el-dialog
        title="学生信息"
        :visible.sync="dialogFormVisible"
        style="margin-left: 400px; width: 800px; overflow: hidden"
      >
        <el-form :model="addstudent">
          <!-- <el-form-item label="学号" :label-width="formLabelWidth">
            <el-input
              v-model="studentFrom.id"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item> -->
          <el-form-item label="姓名" :label-width="formLabelWidth">
            <el-input
              v-model="addstudent.name"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="手机号" :label-width="formLabelWidth">
            <el-input
              v-model="addstudent.phone"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="性别" :label-width="formLabelWidth">
            <!-- <el-input
              v-model="studentFrom.sex"
              autocomplete="off"
              style="width: 200px"
            ></el-input> -->
            <el-radio v-model="addstudent.sex" label="男"></el-radio>
            <el-radio v-model="addstudent.sex" label="女"></el-radio>
          </el-form-item>
          <el-form-item label="家庭地址" :label-width="formLabelWidth">
            <el-input
              v-model="addstudent.home"
              autocomplete="off"
              style="width: 200px"
            ></el-input>
          </el-form-item>
          <el-form-item label="入学日期" :label-width="formLabelWidth">
            <div class="block">
              <el-date-picker
                v-model="addstudent.endata"
                type="date"
                placeholder="选择日期"
                :picker-options="time_option"
                style="width: 200px"
              >
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item label="出生日期" :label-width="formLabelWidth">
            <div class="block">
              <el-date-picker
                v-model="addstudent.birth"
                type="date"
                placeholder="选择日期"
                :picker-options="time_option"
                style="width: 200px"
              >
              </el-date-picker>
            </div>
          </el-form-item>
          <el-form-item label="班级" :label-width="formLabelWidth">
            <!-- <el-input
              v-model="addstudent.clName"
              autocomplete="off"
              style="width: 200px"
            ></el-input> -->
            <el-select v-model="addstudent.classId" style="width: 200px">
              <!-- :label-:想要在选项中展示的内容
              :value:这个选项对应的真正的值（因为存进去的其实是部门id，并不是部门名称。比如示例中区域对应的其实是shanghai）
              :key:与value一致 -->
              <el-option
                v-for="item in options"
                :key="item.id"  
                :label="item.clName"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
    
    <script>
// import axios from "axios";
import request from "@/utils/request";

export default {
  data() {
    return {
       // 今天之后的日期不能选择
       time_option:{
        disabledDate(time){
          return time.getTime() >Date.now();
        }
      },
      inputid: "",
      inputname: "",
      clName: "",
      pageNum: 1, //当前页码
      pageSize: 5, //每页显示的个数
      total: 0,
      dialogFormVisible: false,
      formLabelWidth: "120px",
      ids: [],
      options: [], //装班级
      studentFrom: [
        // {
        //   id: "",
        //   name: "",
        //   phone: "",
        //   sex: "",
        //   endata: "",
        //   home: "",
        //   birth: "",
        //   classId: "",
        //   clName: "",
        // },
      ],
      addstudent: {
        id: "",
        name: "",
        phone: "",
        sex: "",
        endata: "",
        home: "",
        birth: "",
        classId: "",
        clName: "",
      },
    };
  },
  // 页面触发后完成数据渲染
  // mounted() {
  // axios.get("http://localhost:9091/Student/selectAll").then(res =>{
  //     // 显示数据到后台
  //     console.log(res.data)
  //     this.tableData =res.data.data
  // })
  //   request.get("/student/selectALL").then((res) => {
  //     this.studentFrom = res.data;
  //   });
  // },
  created() {
    this.load();
    this.getClassRoom();
  },
  methods: {
    getClassRoom() {
      //获取所有班级的方法
      this.$request.get("/class/selectALL").then((res)=>{
        if (res.code === "200"){
          this.options=res.data
        }
        else{
          this.$message.error(res.msg)
        }
      })
    },
    del(id) {
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request.delete("/student/delete/" + id).then((res) => {
            if (res.code === "200") {
              this.$message.success("删除成功！");
              this.load(1);
            } else {
              this.$message.error(res.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    delbatch() {
      if (!this.ids.length) {
        this.$message.warning("请选择批量删除的数据");
        return;
      }
      this.$confirm("此操作将批量删除这些数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$request
            .delete("/student/delete/batch", {
              data: this.ids,
            })
            .then((res) => {
              if (res.code === "200") {
                this.$message.success("删除成功！");
                this.load(1);
              } else {
                this.$message.error(res.msg);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 获取表格数据
    handleSelectionChange(rows) {
      // 当前选中的所有的行数据
      this.ids = rows.map((v) => v.id);
    },
    handleEdit(row) {
      //编辑数据
      this.addstudent = JSON.parse(JSON.stringify(row));
      this.dialogFormVisible = true;
    },
    handleadd() {
      //新增数据
      this.addstudent = {};
      this.dialogFormVisible = true;
    },
    save() {
      // this.$refs["reform"].validate((valid) => {
      //   if (valid) {
      this.$request({
        url: this.addstudent.id ? "/student/update" : "/student/add",
        method: this.addstudent.id ? "PUT" : "POST",
        data: this.addstudent,
      }).then((res) => {
        if (res.code === "200") {
          this.$message.success("保存成功");
          this.load(1);
          this.dialogFormVisible = false;
        } else {
          this.$message.error(res.msg);
        }
        //   });
        // }
      });
    },
    reset() {
      this.inputid = "";
      this.inputname = "";
      this.clName="";
      this.load();
    },
    load(pageNum) {
      //分页查询
      if (pageNum) {
        this.pageNum = pageNum;
      }
      this.$request
        .get("/student/selectByPage", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            id: this.inputid,
            name: this.inputname,
            clName: this.clName,
          },
        })
        .then((res) => {
          this.studentFrom = res.data.records;
          this.total = res.data.total;
        });
    },
    handleCurrentChange(pageNum) {
      // this.pageNum = pageNum;
      this.load(pageNum);
    },
  },
};
</script>
