<template>
  <body id="poster">
    <el-header>
      <el-row>
        <el-col :span="12"
          ><div class="grid-content bg-purple" style="text-align: left">
            <!-- <router-link to="/Home" > -->
            <strong>学生教务系统</strong>
            <!-- </router-link> -->
          </div></el-col
        >
        <el-col :span="12"
          ><div class="grid-content bg-purple-light" style="text-align: right">
            <el-button type="primary" style="background: #505458; border: none"
              >注册</el-button
            >
            <el-button
              type="primary"
              style="background: #505458; border: none"
              @click="$router.push('/Login')"
              >登录</el-button
            >
          </div></el-col
        >
      </el-row>
    </el-header>
    <el-main>
      <!--
        :model="ruleForm"：将表单的数据与Vue实例的ruleForm进行了双向数据绑定，可以通过v-model来实现表单数据的同步。
        status-icon：在表单项右侧显示状态图标，例：校验成功时显示一个勾号图标。
        :rules="rules"：将表单项的校验规则绑定到Vue实例的rules属性，可以在表单项中使用这些校验规则进行表单的校验。
        ref="ruleForm"：在Vue实例中通过this.$refs.ruleForm引用这个表单，方便在需要时对表单进行操作。
        label-width="100px"：设置表单项标签的宽度为100px。
        class="demo-ruleForm"：自定义表单的样式。
      -->
      <el-form
        :model="RegisterForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="0px"
        label-position="left"
        class="register-Form"
      >
        <h3 class="register-title">注册界面</h3>

        <div style="display: flex">
            <el-form-item label="" prop="auth" style="flex: 1">
            <el-select
              v-model="RegisterForm.auth"
              placeholder="请选择用户类型"
              
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </div>

        <el-form-item label="" prop="username">
          <!-- 输入值  插入图标:prefix-icon ="el-icon-user"-->
          <el-input
            type="text"
            v-model="RegisterForm.username"
            autocomplete="off"
            placeholder="账号"
            prefix-icon="el-icon-user"
            clearable
          >
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="RegisterForm.password"
            autocomplete="off"
            prefix-icon="el-icon-lock"
            placeholder="密码"
            clearable
          >
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPass">
          <el-input
            type="password"
            v-model="RegisterForm.checkPass"
            autocomplete="off"
            prefix-icon="el-icon-lock"
            placeholder="确认密码"
            clearable
          >
          </el-input>
        </el-form-item>

        <el-form-item prop="code">
          <div style="display: flex">
            <el-input
              placeholder="请输入验证码"
              prefix-icon="el-icon-circle-check"
              size="medium"
              style="flex: 1"
              v-model="RegisterForm.code"
              clearable
            ></el-input>
            <div style="flex: 1; height: 36px">
              <!-- 获取验证码的内容 -->
              <valid-code @update:value="getCode" />
            </div>
          </div>
        </el-form-item>

        <el-form-item style="text-align: center">
          <el-button
            type="primary"
            style="background: #505458; border: none"
            @click="register()"
            >注 册</el-button
          >
          <el-button
            type="primary"
            style="background: #505458; border: none"
            @click="resetForm('RegisterForm')"
            >重 置</el-button
          >
          <!-- <el-button
            type="primary"
            style="background: #505458; border: none"
            @click="tologin('ruleForm')"
            >去登录</el-button
          > -->
        </el-form-item>
        <div style="flex: 1">
          已有账号？马上
          <span style="color: #7d807f; cursor: pointer" @click="tologin"
            >登 录</span
          >
        </div>
      </el-form>
    </el-main>
    <!--引入弹窗标签-->
    <!-- <SetUserForma ref="SetUserForma"></SetUserForma> -->
  </body>
</template>
<script>
import ValidCode from "@/components/ValidCode.vue";
// import SetUserForma from "../page/popup/SetUserForma.vue";
// import Header from '../components/Header.vue'
export default {
  name: "Register",
  components: {
    ValidCode,
    // SetUserForma,
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.RegisterForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validateCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入验证码"));
      } else if (value.toLowerCase() != this.code) {
        callback(new Error("验证码错误"));
      } else {
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.RegisterForm.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      code: "", //组件传过来的code
      RegisterForm: {
        username: "",
        password: "",
        checkPass: "",
        url:"@/assets/默认头像.png",
        code: "",
        auth: "",
      },
      options: [
        {
          value: "学生",
          label: "学生",
        },
        {
          value: "教师",
          label: "教师",
        },
        // {
        //   value: "管理员",
        //   label: "管理员",
        // },
      ],
      value: "",
      rules: {
        username: [
          { required: true, message: "请输入账号名称", trigger: "blur" },
          { min: 2, max: 9, message: "长度2-9个字符", trigger: "blur" },
        ],
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        code: [{ validator: validateCode, trigger: "blur" }],
      },
    };
  },
  methods: {
    getCode(code) {
      // this.code = code;
      // 忽略带小写问题
      this.code = code.toLowerCase();
    },
    register() {
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          // 验证通过再发请求

          this.$request.post("/user/register", this.RegisterForm).then((res) => {
            console.log(res);
            if (res.code === "200") {
              // this.$router.push({ path: "/Home" });
              // this.$message.success('嗨！恭喜注册完成~')
              // if (this.RegisterForm.auth === "管理员") {
              //   this.$message({
              //   message: "管理员你好，记得去登陆！",
              //   type: "success",
              // });
              const { username } = res
              this.$message.success("注册成功，您的账号为："+username+"记得去登陆完善个人信息！");
              this.RegisterForm= {};
              // this.$router.push("/Login");
              // }
              // if (this.RegisterForm.auth === "教师") {
              //   this.$message.success("完善个人信息即可注册完成！");
              //   this.$refs.SetUserForma.teacherforma = true;
              // } else if (this.RegisterForm.auth === "教师") {
              //   this.$message.success("完善个人信息即可注册完成！");
              //   this.$refs.SetUserForma.studentforma = true;
              // }
            } else {
              this.$message.error(res.msg);
            }
          });
        }
      });
    },
    // 重置方法
    resetForm(ruleForm) {
      this.$refs[ruleForm].resetFields();
    },
    tologin() {
      this.$message({
        message: "欢迎来到登录页面~",
        type: "success",
      });
      this.$router.push({ path: "/Login" });
    },
  },
};
</script>
  <style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 50px;
}
#poster {
  /* 背景图像的起始位置:top、left、right、bottom、center */
  background-position: center;
  height: 100%;
  width: 100%;
  /*
          规定背景图像的尺寸
          1.length:像素直接设置宽和高
          2.percentage：百分比直接设置宽和高
          3.cover：背景图片覆盖背景区域
          4.contain：把图片扩展到最大尺寸，适应内容区域
          5.no-repeat:背景图像仅显示一次
          */
  background-size: cover;
  /* 规定元素的定位类型
          1.static：默认值
          2.absolute：绝对定位
          3.relative：相对定位，正常位置进行定位
          4.fixed：绝对定位，相对于浏览器窗口定位
          5.inherit：规定应该从父元素继承 position 属性的值*/
  position: fixed;
  margin: 0px;
  padding: 0px;
}
.register-Form {
  /* 允许为元素添加圆角边框 */
  border-radius: 15px;
  background-clip: padding-box;
  margin: 60px auto;
  /* margin-left: 50%; */
  width: 400px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  /* 设置所有的边框属性:大小,样式,颜色*/
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  justify-content: center;
  align-items: center;
}
.register-title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
/* a指文字不带下划线且为白色，router-link-active指点击后变为什么颜色。 */
.router-link-active {
  text-decoration: none;
  color: rgb(6, 6, 6);
}
a {
  text-decoration: none;
  color: rgb(7, 7, 7);
}
</style>
